import React from 'react';
// import classnames from 'classnames';
import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
import TextFieldGroup from '../../common/TextFieldGroup';

// console.log(resolvedSchema);

class DateTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: ''
    };
  }

  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid() {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }

  onSubmit = (e) => {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }
  render() {
    let { value } = this.props.data;
    let isLoading = false;
    return (
      <form onSubmit={this.onSubmit}>
        <h2>DateType</h2>
        <p>resolvedSchema.description</p>

        <TextFieldGroup label="Value"
          onChange={this.onChange}
          value={value}
          title="Required value for the date."
          field="value"/>

        <div className="form-group">
          <button disabled={this.state.isLoading} className="btn btn-primary btn-lg">
            View JSON
          </button>
        </div>
      </form>

    );
  }
}
function validateInput(data) {
  let errors = {};

  if (Validator.isEmpty(data.value)) {
    errors.value = 'This field is required';
  }

  return {errors, isValid: isEmpty(errors)};
}
export default DateTypeForm;



